const UISelectors = {
  itemList: '#item-list',
  addBtn: '.add-btn',
  updateBtn: '.update-btn',
  deleteBtn: '.delete-btn',
  backBtn: '.back-btn',
  itemNameInput: '#item-name',
  itemCaloriesInput: '#item-calories',
  form: '#form',
  totalCalories: '#total-calories'
};

const UIElements = {
  itemList: document.querySelector(UISelectors.itemList),
  addBtn: document.querySelector(UISelectors.addBtn),
  updateBtn: document.querySelector(UISelectors.updateBtn),
  deleteBtn: document.querySelector(UISelectors.deleteBtn),
  backBtn: document.querySelector(UISelectors.backBtn),
  inputForm: document.querySelector(UISelectors.form),
  itemNameInput: document.querySelector(UISelectors.itemNameInput),
  itemCaloriesInput: document.querySelector(UISelectors.itemCaloriesInput),
  totalCalories: document.querySelector(UISelectors.totalCalories)
};

const UICtrl = {
  populateItemList: function(items) {
    let html = '';
    items.forEach(item => {
      html += `
      <li class="collection-item" id="item-${item.id}">
        <strong>${item.name}: </strong> ${item.calories} Calories
        <a href="#" class="secondary-content">
          <i class="fa fa-pencil edit-item"></i>
        </a>
      </li>
      `;
    });
    UIElements.itemList.innerHTML = html;
  },
  getItemInput: function() {
    return {
      name: UIElements.itemNameInput.value,
      calories: UIElements.itemCaloriesInput.value
    };
  },
  addListItem: function(item) {
    // show the list
    UIElements.itemList.style.display = 'block';

    // create li item
    const li = document.createElement('li');
    li.className = 'collection-item';
    li.id = `item-${item.id}`;
    li.innerHTML = `
    <strong>${item.name}: </strong> <em>${item.calories} Calories</em>
    <a href="#" class="secondary-content">
      <i class="fa fa-pencil edit-item"></i>
    </a>
    `;

    // insert item
    UIElements.itemList.insertAdjacentElement('beforeend', li);
  },
  clearInput: function() {
    UIElements.inputForm.reset();
  },
  hideList: function() {
    UIElements.itemList.style.display = 'none';
  },
  showTotalCalories: function(total) {
    UIElements.totalCalories.textContent = total;
  },
  clearEditState: function() {
    UICtrl.clearInput();
    UIElements.updateBtn.style.display = 'none';
    UIElements.deleteBtn.style.display = 'none';
    UIElements.backBtn.style.display = 'none';
    UIElements.addBtn.style.display = 'inline';
  },
  getElements: function() {
    return UIElements;
  }
};

export default UICtrl;
