if(process.env.NODE_ENV === 'development') {
  require('../index.html');
}
require('./css/styles.css');

import ItemCtrl from './modules/itemCtrl.js';
import UICtrl from './modules/uiCtrl.js';

const App = (function(ItemCtrl, UICtrl) {
  // Load event listeners
  const loadEventListeners = function() {
    const UIElements = UICtrl.getElements();

    // add item event
    UIElements.addBtn.addEventListener('click', itemAddSubmit);
    UIElements.itemList.addEventListener('click', itemUpdateSubmit);
  };

  const itemAddSubmit = function(e) {
    // get form input from UI Controller
    const input = UICtrl.getItemInput();

    // check for name and calorie input
    if(input.name !== '' && input.calories !== '') {
      // add item
      const newItem = ItemCtrl.addItem(input.name, input.calories);

      // add item to UI list
      UICtrl.addListItem(newItem);

      // update total calories
      const totalCalories = ItemCtrl.getTotalCalories();
      UICtrl.showTotalCalories(totalCalories);

      // clear fields
      UICtrl.clearInput();
    }

    e.preventDefault();
  };

  const itemUpdateSubmit = function(e) {
    if(e.target.classList.contains('edit-item')) {
      // get id of li
      const listId = e.target.parentNode.parentNode.id;
      const id = listId.split('-')[1];

      // set current item
      const itemToEdit = ItemCtrl.getItemById(parseInt(id));
      ItemCtrl.setCurrentItem(itemToEdit);
    } else {

    }
    e.preventDefault();
  };

  return {
    init: function() {
      // set initial state
      UICtrl.clearEditState();

      // fetch items from data structure
      const items = ItemCtrl.getItems();

      // Check if any items
      if(items.length === 0) {
        UICtrl.hideList();
      } else {
        // populate list with items
        UICtrl.populateItemList(items);
      }

      // update total calories
      const totalCalories = ItemCtrl.getTotalCalories();
      UICtrl.showTotalCalories(totalCalories);

      loadEventListeners();
    }
  };
})(ItemCtrl, UICtrl);

App.init();
